<h2 id="_1、查看mysql的安装情况" tabindex="-1"><a class="header-anchor" href="#_1、查看mysql的安装情况" aria-hidden="true">#</a> 1、查看mysql的安装情况</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">rpm</span> -qa <span class="token operator">|</span> <span class="token function">grep</span> -i mysql


<span class="token punctuation">[</span>root@iZbp17xtpz52b5n1iuos0sZ java-test<span class="token punctuation">]</span><span class="token comment"># rpm -qa | grep -i mysql</span>
bt-mysql57-5.7.34-1.el7.x86_64
<span class="token punctuation">[</span>root@iZbp17xtpz52b5n1iuos0sZ java-test<span class="token punctuation">]</span><span class="token comment">#</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="_2、删除上图安装的软件-停止mysql服务" tabindex="-1"><a class="header-anchor" href="#_2、删除上图安装的软件-停止mysql服务" aria-hidden="true">#</a> 2、删除上图安装的软件(停止mysql服务)</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">rpm</span> -ev bt-mysql57-5.7.34-1.el7.x86_64 --nodeps

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="_3、都删除成功之后-查找相关的mysql的文件" tabindex="-1"><a class="header-anchor" href="#_3、都删除成功之后-查找相关的mysql的文件" aria-hidden="true">#</a> 3、都删除成功之后，查找相关的mysql的文件</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">find</span> / -name mysql


<span class="token punctuation">[</span>root@iZbp17xtpz52b5n1iuos0sZ java-test<span class="token punctuation">]</span><span class="token comment"># find / -name mysql</span>
/www/server/panel/pyenv/lib/python3.7/site-packages/sqlalchemy/dialects/mysql
/www/server/mysql
/www/server/mysql/bin/mysql
/www/server/mysql/include/mysql
/www/server/data/mysql
/var/spool/mail/mysql
/run/lock/subsys/mysql
/etc/selinux/targeted/active/modules/100/mysql
/usr/lib64/mysql
/usr/share/mysql
/usr/bin/mysql
/usr/include/mysql
<span class="token punctuation">[</span>root@iZbp17xtpz52b5n1iuos0sZ java-test<span class="token punctuation">]</span><span class="token comment"># </span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h2 id="_4、删除全部文件" tabindex="-1"><a class="header-anchor" href="#_4、删除全部文件" aria-hidden="true">#</a> 4、删除全部文件</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">rm</span> -rf /www/server/panel/pyenv/lib/python3.7/site-packages/sqlalchemy/dialects/mysql
<span class="token function">rm</span> -rf /www/server/mysql
<span class="token function">rm</span> -rf /www/server/mysql/bin/mysql
<span class="token punctuation">..</span>.
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h2 id="_5、再次执行命令" tabindex="-1"><a class="header-anchor" href="#_5、再次执行命令" aria-hidden="true">#</a> 5、再次执行命令</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">rpm</span> -qa <span class="token operator">|</span> <span class="token function">grep</span> -i mysql
